import React, { useState } from 'react';

import ObjectsCanvas from '../gltfcanvas/objectscanvas';

function Main() {


  const [objectTrhreejs, setObject] = useState('RED WITHout  WHEEL low poly');

  const handleImgone = () => {
    setObject('redlowpoly');
  }

  const handleimgs = () => {
    setObject('RED WITHout  WHEEL low poly');
  }

  const handleimg3 = () => {
    setObject('WHITE WITH WHEELlow poly');
  }
  const handleimg4 = () => {
    setObject('WHITE WITHOUT WHEEL');
  }

  return (
    <div>

      <div className='d-flex justify-content-center my-3'>
        <button onClick={handleImgone} className='btn btn-success rounded-0'>Img 1</button>
        <button onClick={handleimgs} className='btn btn-success rounded-0 mx-md-2'>Img 2</button>
        <button onClick={handleimg3} className='btn btn-success rounded-0 mx-md-2'>Img 3</button>
        <button onClick={handleimg4} className='btn btn-success rounded-0'>Img 4</button>
      </div>
      <div className="objectCanvas">
        <ObjectsCanvas objectsThreejs={objectTrhreejs} />
      </div>

    </div>

  )
}

export default Main